header.row {
	margin: 0;
}

pre {
	white-space: -moz-pre-wrap;
	white-space: -o-pre-wrap;
	white-space: -pre-wrap;
	white-space: pre-wrap;
	word-wrap: break-word;
}

section.row {
	margin-bottom: 45px;
}

.page-title {
	display: inline-block;
	font-weight: bold;
	margin-bottom: 25px;

	small {
		display: block;
		margin-top: 8px;
	}
}

.clay-site-row-spacer {
	margin-bottom: 20px;
}

#headerTopBar {
	background: #0058BA url(../../images/back_decoration_nav.png) no-repeat 100% 0;
	position: relative;

	a {
		color: #FFF;
		margin-right: 8px;
		vertical-align: middle;

		&:hover {
			color: rgba(255, 255, 255, 0.8);
			text-decoration: none;
		}
	}

	.container {
		position: relative;
	}

	.title {
		float: left;
		font-size: 1.5rem;
		margin-bottom: 12.5px;
		margin-top: 12.5px;

		@include media-breakpoint-up(md) {
			margin-left: 18px;
		}

		.lexicon-version {
			font-size: 60%;
			vertical-align: middle;
		}
	}
}

#siteNavToggle {
	position: absolute;
	right: 65px;
	top: 50%;
	transform: translateY(-50%);
}

.hide-site-nav {
	#mainNav {
		@media (min-width: 768px) {
			display: none;
		}
	}

	#mainContent {
		@media (min-width: 768px) {
			padding-left: 0;
		}
	}

	.main-nav-container .col-md-12 {
		padding-left: 0;
		padding-right: 0;
	}
}

#siteConfig {
	position: absolute;
	right: 15px;
	top: 50%;
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);
	z-index: 5001;

	.dropdown-menu {
		width: 240px;
	}
}

#skipTo {
	left: 15px;
	min-width: 170px;
	position: absolute;
	top: 50%;
	-ms-transform: translateY(-50%);
	transform: translateY(-50%);
	z-index: -1;

	&:focus {
		z-index: 1;
	}
}

#claySiteMobileNavigationToggle {
	float: left;
	height: 32px;
	margin-right: 8px;
	padding: 0;
	position: relative;
	top: 50%;
	transform: translateY(-50%);
	width: 32px;
}

.docs-icon-bar {
	background-color: #FFF;
	display: block;
	height: 2px;
	margin-left: auto;
	margin-right: auto;
	width: 22px;

	+ .docs-icon-bar {
		margin-top: 4px;
	}
}

.animated-toggle .docs-icon-bar {
	position: relative;
	top: 0;
	transition-delay: 0s 0s 0.2s 0s;
	transition-duration: 0.2s;
	transition-property: background, opacity, top, transform;
	transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
}

.main-nav-open {
	.animated-toggle {
		.docs-icon-bar-1,
		.docs-icon-bar-3 {
			top: 6px;
			-ms-transform: rotate(45deg);
			transform: rotate(45deg);
			transition-delay: 0s 0s 0s 0.2s;
		}

		.docs-icon-bar-3 {
			top: -6px;
			-ms-transform: rotate(-45deg);
			transform: rotate(-45deg);
		}

		.docs-icon-bar-2 {
			background-color: transparent;
			opacity: 0;
		}
	}
}

#imageShapes img {
	display: block;
	max-height: 150px;
	max-width: 150px;
	width: 100%;
}

.clay-site-light-container {
	background-color: #000;
	display: inline-block;
	padding: 4px;
	vertical-align: baseline;
}

// Breakpoints

.clay-site-breakpoint-min-xs { content: '#{map-get($grid-breakpoints, xs)}'; }
.clay-site-breakpoint-min-sm { content: '#{map-get($grid-breakpoints, sm)}'; }
.clay-site-breakpoint-min-md { content: '#{map-get($grid-breakpoints, md)}'; }
.clay-site-breakpoint-min-lg { content: '#{map-get($grid-breakpoints, lg)}'; }
.clay-site-breakpoint-min-xl { content: '#{map-get($grid-breakpoints, xl)}'; }

.clay-site-breakpoint-max-xs { content: '#{map-get($grid-breakpoints, sm) - 1}'; }
.clay-site-breakpoint-max-sm { content: '#{map-get($grid-breakpoints, md) - 1}'; }
.clay-site-breakpoint-max-md { content: '#{map-get($grid-breakpoints, lg) - 1}'; }
.clay-site-breakpoint-max-lg { content: '#{map-get($grid-breakpoints, xl) - 1}'; }

.clay-site-inline-scroller-height { content: '#{$inline-scroller-max-height}'; }

// Colors Page

.clay-site-swatch {
	display: block;
	padding: 0.5rem 1rem;
}

.clay-site-hex {
	margin-left: 6px;
	margin-right: 12px;
	text-transform: uppercase;
}

.clay-site-hex,
.clay-site-rgb,
.clay-site-var {
	-moz-user-select: all;
	-ms-user-select: all;
	-webkit-user-select: all;
	user-select: all;
}

.clay-site-swatch-text {
	&::-moz-selection {
		background-color: #FFF;
	}

	&::selection {
		background-color: #FFF;
	}
}

// Colors Page (Grays)

@mixin clay-site-selection($color) {
	&::-moz-selection {
		background-color: color-yiq($color);
		color: color-yiq(color-yiq($color));
	}

	&::selection {
		background-color: color-yiq($color);
		color: color-yiq(color-yiq($color));
	}
}

.clay-site-swatch-white {
	background-color: $white;
	color: color-yiq($white);
	content: '#{$white}';

	.clay-site-var {
		content: '$white';
	}

	.clay-site-swatch-text {
		@include clay-site-selection($white);
	}
}

