// Dropdowns Page

.clay-site-dropdown-display {
	.clay-site-dropdown-menu-container {
		float: left;
		margin-right: 15px;
		min-height: 285px;
	}

	.dropdown-menu {
		display: block;
		position: static;
		top: auto;
		z-index: 0;
	}

	.dropdown-menu-horizontal {
		display: flex;
	}

	.dropdown-wide-container {
		float: left;

		.dropdown-menu {
			display: flex;
		}
	}

	.mega-menu .dropdown-menu {
		float: left;
		max-width: 100%;
	}
}

// Grid

.clay-site-grid-width {
	&,
	.clay-site-grid-width-sm,
	.clay-site-grid-width-md,
	.clay-site-grid-width-lg,
	.clay-site-grid-width-xl,
	.clay-site-grid-width-xxl {
		display: none;
	}
}

.clay-site-container-fluid-switcher .btn {
	margin-bottom: 0.5rem;
}

.container-fluid .clay-site-grid-width {
	display: block;
}

.container-fluid-max-sm .clay-site-grid-width-sm,
.container-fluid-max-md .clay-site-grid-width-md,
.container-fluid-max-lg .clay-site-grid-width-lg,
.container-fluid-max-xl .clay-site-grid-width-xl,
.container-fluid-max-xxl .clay-site-grid-width-xxl {
	display: inline;
}

@each $breakpoint, $value in $container-max-widths {
	.container-fluid-max-#{$breakpoint} .clay-site-grid-width-#{$breakpoint} {
		&:after {
			content: ' #{$value}';
		}
	}

	[data-container-classes="container-fluid container-fluid-max-#{$breakpoint} main-nav-container"] {
		&:after {
			content: ' (#{$value})';
		}
	}
}

.modal-open .main-nav-open {
	-webkit-overflow-scrolling: auto;
}

