// Icons (Lexicon Page)

.lexicon-icon-examples {
	display: block;
	padding: 35px 20px 20px;
	text-align: center;

	.lexicon-icon {
		height: 160px;
		margin: 0 5%;
		transition: all 0.1s ease-in;
		width: 140px;
	}

	.lexicon-icon-asterisk {
		fill-opacity: 0;
		stroke: #078DD8;
		stroke-width: 10px;

		&:hover {
			fill: #078DD8;
			fill-opacity: 1;
			stroke: #000;
			stroke-opacity: 0.3;
			stroke-width: 5px;
		}
	}

	.lexicon-icon-logout {
		fill: #DA3A35;
		fill-opacity: 1;
		stroke: #FFF;
		stroke-width: 55px;

		&:hover {
			fill-opacity: 1;
			stroke: #900;
			stroke-opacity: 0.5;
			stroke-width: 25px;
		}
	}

	@keyframes exclamationHover {
		0% {
			transform: scale(1);
		}

		60% {
			stroke-width: 10px;
			transform: scale(1.5);
		}

		100% {
			transform: scale(1);
		}
	}

	.lexicon-icon-exclamation-full {
		fill: #E3A200;
		stroke: #FFF;
		stroke-opacity: 0.4;
		stroke-width: 40px;

		&:hover {
			animation: exclamationHover 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275);
			animation-fill-mode: both;
			fill: #EC297B;
		}
	}
}

