// Popovers Page

.clay-site-popover-display-column {
	display: inline-block;
	height: 240px;
	position: relative;
	width: 250px;
}

// Progress Bar Page

.clay-site-progress-bar-page {
	.progress {
		margin-bottom: 15px;
	}

	.progress-group {
		margin-bottom: 15px;

		.progress {
			margin-bottom: 0;
		}
	}
}

// Stickers

.clay-site-positional-sticker {
	max-width: 300px;
}

// Tables Page

#verticalAlignTableListDemo {
	background-color: #F5F7F8;

	tr {
		height: 120px;
	}
}

// Tooltips Page

.clay-site-tooltip-display-column {
	display: inline-block;
	height: 100px;
	position: relative;
	width: 250px;
}

// Typography Page

.clay-site-sans-serif { font-family: $font-family-sans-serif; }
.clay-site-sans-serif::after { content: '#{$font-family-sans-serif}'; }

.clay-site-serif { font-family: $font-family-serif; }
.clay-site-serif::after { content: '#{$font-family-serif}'; }

.clay-site-monospace { font-family: $font-family-monospace; }
.clay-site-monospace::after { content: '#{$font-family-monospace}'; }

// Site Main Nav

.side-navigation-data-api-buttons {
	margin-bottom: 15px;

	.btn {
		margin-bottom: 3px;
	}
}

.side-navigation-methods {
	h4 {
		margin-top: 2em;

		&:nth-of-type(1) {
			margin-top: 1em;
		}
	}
}

.menu-toggle {
	color: #FFF;
	display: block;
	float: left;
	padding: 10px 15px;
	text-align: left;

	.glyphicon {
		font-size: 22px;
	}
}

// Main Nav Structure

$mainNavWidth: 220px;

.lexicon-site-body-fixed {
	bottom: 0;
	left: 0;
	position: fixed;
	right: 0;
	top: 0;

	@include media-breakpoint-up(md) { // min-width 768px
		position: static;
	}
}

#mainContent {
	left: 0;
	position: relative;

	@include media-breakpoint-up(md) { // min-width 768px
		padding-left: $mainNavWidth + 20;
	}

	[dir="rtl"] & {
		@include media-breakpoint-up(md) { // min-width 768px
			padding-left: 0;
			padding-right: $mainNavWidth + 20;
		}
	}
}

#mainNav {
	overflow: hidden;
	position: absolute;
	visibility: hidden;
	width: 0;

	@include media-breakpoint-up(md) { // min-width 768px
		visibility: visible;
		width: $mainNavWidth;
	}
}

.main-nav-container {
	position: relative;
}

.main-nav-inner {
	padding: 3px;
	width: $mainNavWidth;
}

.main-nav-open {
	-webkit-overflow-scrolling: touch;
	height: 100%;
	overflow-x: hidden;

	@include media-breakpoint-up(md) { // min-width 768px
		overflow-x: visible;
	}

	#mainNav {
		visibility: visible;
		width: $mainNavWidth;
	}

	#mainContent {
		left: $mainNavWidth + 10;

		@include media-breakpoint-up(md) { // min-width 768px
			left: auto;
			position: static;
		}
	}
}

.main-nav-transition {
	#mainNav {
		visibility: visible;
	}

	#mainNav,
	#mainContent {
		transition: all 0.25s ease;
	}
}

// Main Nav Skin

#mainNav {
	padding-top: 1em;
	z-index: 1;

	li {
		> a {
			&:hover, &:focus {
				background-color: transparent;
				text-decoration: underline;
			}
		}

		&.child-active, &.active {
			> a {
				color: #0058BA;
				font-weight: bold;
			}
		}
	}

	.main-nav-section li {
		.nav-title {
			display: block;
			padding: 10px 15px;
		}

		> h4 {
			color: #F3F3F3;
			padding: 10px 15px;
		}

		div > .main-nav-section {
			border-left: 3px solid #E4E4E4;
			margin-left: 15px;

			li {
				> a {
					font-size: 0.9em;
					padding: 2px;
					padding-left: 10px;
				}

				&.child-active, &.active {
					> a {
						border-left: 3px solid #0058BA;
						margin-left: -3px;
					}
				}
			}
		}

		&.nav-static-link > .nav-title {
			padding-top: 0;
		}
	}
}

// Metalsmith Headings Identifiers

h1, h2, h3, h4, h5, h6 {
	.heading-anchor {
		align-items: center;
		color: inherit;
		cursor: pointer;
		display: inline-flex;
		float: right;
		font-size: 14px;
		height: 32px;
		justify-content: center;
		line-height: 1;
		text-decoration: none;
		vertical-align: middle;
		width: 32px;

		.lexicon-icon {
			margin-top: 0;
		}
	}

	@include media-breakpoint-up(md) { // min-width 768px
		.heading-anchor {
			float: none;
			margin-left: -32px;
			opacity: 0;

			&:focus {
				opacity: 1;
			}
		}

		&:hover .heading-anchor {
			opacity: 1;
		}
	}
}

// Side Navigation Component Page

body {
	left: 0;
	position: relative;
}

body.open {
	overflow: hidden;

	@media (min-width: 768px) {
		left: 320px;
	}
}

body.sidenav-transition {
	overflow: hidden;
}

#mySidebar {
	background-color: #222328;
	box-shadow: -3px 0 3px -3px #000 inset;
	color: #F8F8F8;

	ul {
		border-bottom: 1px solid #3E3E3E;
		border-top: 1px solid #000;

		li {
			border-bottom: 1px solid #000;
			border-top: 1px solid #3E3E3E;
			margin-top: 0;

			a {
				background-color: #2C2D32;
				box-shadow: -3px 0 3px -3px #000 inset;

				&, &:hover, &:focus {
					color: #B3B6BC;
					font-weight: 200;
					text-shadow: 0 1px 0 #000;
				}
			}
		}
	}
}

#mySidebar h3 {
	margin: 0;
	padding: 15px;
}

#sidenavDemo {
	margin-top: 20px;

	h4 {
		border-bottom: 2px dotted #AEAEAE;
		color: #BA3A37;
		margin: 20px 0;
		padding-bottom: 8px;
	}

	.sidenav-main-content h3 {
		border-bottom: 1px solid #1E2639;
		color: #F8F8F8;
		margin: 0 -20px;
		padding: 15px;
	}
}

.sidenav-demo-container {
	overflow: hidden;
	padding-top: 3px;
}

// Side Navigation RTL

[dir="rtl"] {
	.sidenav-fixed > .sidenav-menu-slider {
		left: auto;
		right: 0;
	}

	.sidenav-fixed.sidenav-right > .sidenav-menu-slider {
		left: 0;
		right: auto;
	}

	.sidenav-container.closed > .sidenav-content {
		right: 0;
	}

	.sidenav-container.sidenav-right {
		.sidenav-content {
			left: 0;
			right: auto;
		}

		.sidenav-menu {
			left: 0;
			right: auto;
		}

		.sidenav-menu-slider {
			left: 0;
			right: auto;
		}
	}

	.sidenav-fixed.sidenav-menu-slider.sidenav-right {
		left: 0;
		right: auto;
	}
}

