// Close

$close: (
	border-radius: 100px,
	color: $gray-600,
	opacity: 1,
	text-decoration: none,
	hover-bg: rgba($black, 0.03),
	hover-color: $gray-600,
	hover-opacity: 1,
	focus-bg: rgba($black, 0.12),
	focus-box-shadow: none,
	focus-opacity: 1,
	focus-outline: 0,
	disabled-opacity: 0.5,
	btn-focus-box-shadow: none,
	height: 3rem,
	width: 3rem,
);

// Inputs

$input-height: 3.5rem;

$input-bg: $gray-100;
$input-border-bottom-width: 0.0625rem;
$input-border-left-width: 0;
$input-border-right-width: 0;
$input-border-top-width: 0;
$input-border-color: rgba($black, 0.42);
$input-border-radius: 0.25rem 0.25rem 0 0;
$input-box-shadow: none;
$input-color: rgba($black, 0.87);
$input-letter-spacing: 0.009375em;
$input-padding-x: 1rem;

$input-transition: background-color 15ms linear, background-position 180ms cubic-bezier(0.4, 0, 0.2, 1), background-size 180ms cubic-bezier(0.4, 0, 0.2, 1), border-color 180ms cubic-bezier(0.4, 0, 0.2, 1);

$input-focus-bg: $gray-300;
$input-focus-border-color: transparent;
$input-focus-box-shadow: none;

$input-disabled-bg: $gray-50;
$input-disabled-border-color: rgba($black, 0.06);
$input-disabled-color: rgba($black, 0.37);

$input: (
	bg-position: 53% calc(100% + #{$input-border-bottom-width}),
	bg-repeat: no-repeat,
	bg-size: 50% 2px,
	hover-bg: $gray-200,
	hover-border-color: rgba($black, 0.87),
	focus-bg: $input-focus-bg,
	focus-bg-image: linear-gradient($primary-a700 100%, transparent 0%),
	focus-border-color: $input-focus-border-color,
	disabled-bg: $input-disabled-bg,
	disabled-border-color: $input-disabled-border-color,
	disabled-color: $input-disabled-color,
);

$input-readonly: (
	bg: $input-bg,
	border-color: $input-border-color,
	hover-bg: $input-bg,
	hover-border-color: $input-border-color,
	focus-bg: $input-bg,
);

$input-border-radius-sm: 0.25rem 0.25rem 0 0;
$input-height-sm: 2.5rem;
$input-padding-x-sm: 1rem;

$input-border-radius-lg: 0.25rem 0.25rem 0 0;
$input-height-lg: 5rem;
$input-padding-x-lg: 1.25rem;

$input-select-icon: clay-icon(caret-bottom, $gray-600), none;
$input-select-bg-position: right $input-padding-x center, map-get($input, bg-position);
$input-select-bg-size: 1rem, map-get($input, bg-size);
$input-select-padding-right: 2.5rem;

$input-select-icon-focus: clay-icon(caret-bottom, $gray-600), map-get($input, focus-bg-image);

$input-select-icon-disabled: clay-icon(caret-bottom, $input-disabled-color), none;

$mdc-input-outline: (
	bg: $white,
	border-radius: 4px,
	border-width: 1px,
	transition: none,
	hover-bg: $white,
	focus-bg: $white,
	focus-border-color: $primary-a700,
	focus-box-shadow: inset 0 0 0 1px $primary-a700,
	disabled-bg: $white,
	disabled-border-color: $input-disabled-border-color,
	disabled-color: $input-disabled-color,
);

// Checkbox

$input-label-color: rgba($black, 0.87);

$custom-control-margin-bottom: 1.75rem;

$custom-control-description-font-size: 0.875rem;
$custom-control-description-padding-left: 1rem;

$custom-control-indicator-border-color: $gray-600;
$custom-control-indicator-border-width: 0.125rem;
$custom-control-indicator-bg: transparent;
$custom-control-indicator-size: 1.125rem;

$custom-control-indicator-focus-border-color: $custom-control-indicator-border-color;

$custom-control-indicator-active-bg: transparent;
$custom-control-indicator-active-border-color: $custom-control-indicator-border-color;

$custom-control-label-disabled-color: $input-label-color;
$custom-control-indicator-disabled-bg: transparent;
$custom-control-indicator-disabled-border-color: rgba($black, 0.26);

$custom-control-indicator-checked-bg: $primary-a700;
$custom-control-indicator-checked-color: $white;
$custom-control-indicator-checked-border-color: $custom-control-indicator-checked-bg;

$custom-control-indicator-checked-active-bg: $custom-control-indicator-checked-bg;
$custom-control-indicator-checked-active-border-color: $custom-control-indicator-checked-bg;

$custom-control-indicator-checked-disabled-bg: rgba($black, 0.26);
$custom-control-indicator-checked-disabled-border-color: transparent;

$custom-checkbox-indicator-border-radius: 0.125rem;
$custom-checkbox-indicator-icon-checked: clay-svg-url('<svg  xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24"><path fill="none" d="M2,13 8,19 23,5" stroke="#{$custom-control-indicator-checked-color}" stroke-width="3"></path></svg>');
$custom-checkbox-indicator-icon-checked-bg-size: 0.875rem 0.875rem;
$custom-checkbox-indicator-indeterminate-bg: $primary-a700;
$custom-checkbox-indicator-icon-indeterminate-bg-size: 0.625rem 0.625rem;

$custom-radio-indicator-icon-checked: clay-icon(circle, $primary-a700);
$custom-radio-indicator-icon-checked-bg-size: 0.625rem 0.625rem;

$custom-radio-indicator-checked-disabled-border-color: rgba($black, 0.26);

// Lexicon Labels are equivalent to Material Design Chips
// We start by preparing the base component. The base chip height is 32px. Label height is a function of font-size, line-height, border-width, and padding bottom and top.

$label-border-color: transparent;
$label-border-radius: 100px;
$label-font-size: 0.875rem;
$label-font-weight: $font-weight-normal;
$label-line-height: 1;
$label-padding-x: 0.75rem;
$label-padding-y: 0.625rem;

$label-secondary: (
	bg: $gray-200,
	color: $gray-900,
	border-color: $gray-200,
);


// mdc-ripple-upgraded--background-focused mdc-ripple-upgraded--foreground-activation

@import "functions/_global-functions";

@import "variables/_bs4-variable-overwrites";

@import "bootstrap/bootstrap";

@import "_variables";

@import "_mixins";

@import "_components";

// Unfortunately some properties aren't configurable via variables in 2.x, for cases like these we need to add our own CSS to change them.

// Getting Started

code {
	font-family: $font-family-monospace;
}

pre {
	background: $white;
	border: 1px solid #ddd;
	font-size: 0.8125rem;
	font-family: $font-family-monospace;
	line-height: 1.25rem;
	padding: 0.625rem 1.5625rem 0.625rem 1.25rem;

	code {
		color: $code-color;
		font-size: $code-font-size;
	}
}

// Buttons

.btn {
	letter-spacing: 0.0892857143em;
	text-transform: uppercase;
}

.btn-monospaced {
	letter-spacing: normal;
}

.btn-unstyled {
	letter-spacing: normal;
	text-transform: none;
}

.btn-primary,
.btn-success,
.btn-info,
.btn-warning,
.btn-danger,
.btn-dark {
	box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2), 0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0,0,0,.12);

	&:hover {
		box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2)#{','} 0px 4px 5px 0px rgba(0, 0, 0, 0.14)#{','} 0px 1px 10px 0px rgba(0, 0, 0, 0.12);
	}

	&:disabled {
		box-shadow: none;
	}
}

.form-control {
	letter-spacing: $input-letter-spacing;

	@include clay-form-control-variant($input);

	&:focus,
	&.focus {
		background-size: 100% 2px,
	}
}

select.form-control:not([multiple]):not([size]),
.form-control-select {
	&:focus,
	&.focus {
		background-size: 1rem, 100% 2px;
	}
}

.mdc-form-control-outline {
	@include clay-form-control-variant($mdc-input-outline);
}

// This needs to be removed once upgraded to 2.18.2

.form-control[readonly] {
	@include clay-form-control-variant($input-readonly);
}

// CUSTOM CONTROL INPUT

.custom-control-label {
	&::before {
		background-position: center;
		background-repeat: no-repeat;
	}
}

.custom-control .custom-control-input {
	transform: scale(2.5);
}

.custom-checkbox {
	.custom-control-input {
		~ .custom-control-label {
			&::before {
				background-position: 0;
				background-size: 0 nth($custom-checkbox-indicator-icon-checked-bg-size, 2);
				transition: background-size 90ms linear;
			}
		}

		&:checked ~ .custom-control-label {
			&::before {
				background-image: $custom-checkbox-indicator-icon-checked;
				background-size: $custom-checkbox-indicator-icon-checked-bg-size;
			}
		}

		&:indeterminate ~ .custom-control-label {
			&::before {
				background-image: $custom-checkbox-indicator-icon-indeterminate;
				background-position: 2px;
				background-size: $custom-checkbox-indicator-icon-indeterminate-bg-size;
			}
		}
	}
}

.custom-radio {
	.custom-control-input {
		height: 1.25rem;
		width: 1.25rem;

		~ .custom-control-label {
			&::before {
				background-size: 0 nth($custom-radio-indicator-icon-checked-bg-size, 2);
				height: 1.25rem;
				left: -0.0625rem;
				top: 0.1875rem;
				width: 1.25rem;
				transition: background-size 90ms linear;
			}
		}

		&:checked ~ .custom-control-label {
			&::before {
				background-color: transparent;
				background-image: $custom-radio-indicator-icon-checked;
				background-size: $custom-radio-indicator-icon-checked-bg-size;
			}
		}

		&:checked:disabled ~ .custom-control-label {
			&::before {
				background-color: transparent;
				background-image: clay-icon(circle, rgba($black, 0.26));
			}
		}
	}
}

@keyframes custom-control-ripple {
	0% {
		background-image: clay-icon(circle, rgba($primary-a700, 0.12));
		background-size: 0 0;
	}
	90% {
		background-image: clay-icon(circle, rgba($primary-a700, 0.12));
		background-size: 100% 100%;
	}
	100% {
		background-image: clay-icon(circle, rgba($primary-a700, 0));
		background-size: 100% 100%;
	}
}

.custom-control.custom-checkbox,
.custom-control.custom-radio {
	.custom-control-input {
		~ .custom-control-label::after {
			background-size: 0 0;
			background-repeat: no-repeat;
			background-position: center;
			border-radius: 100px;
			display: block;
			transform: scale(2.5);
		}

		&:hover {
			~ .custom-control-label::after {
				background-color: rgba($primary-a700, 0.04);
			}
		}

		&:focus {
			~ .custom-control-label::after {
				animation-name: custom-control-ripple;
				animation-duration: 310ms;
				animation-fill-mode: forwards;
				animation-timing-function: cubic-bezier(0.4, 0, 0.6, 1);
				background-color: rgba($primary-a700, 0.12);
			}
		}

		&:active {
			~ .custom-control-label::after {
				animation-name: none;
				background-color: rgba($primary-a700, 0.24);
			}
		}

		&:disabled {
			~ .custom-control-label::after {
				opacity: 0;
			}
		}
	}
}

